import React, { Component } from 'react';
import 'bootstrap/dist/css/bootstrap.css';
import web3 from './web3';
import crowdfunding from './crowdfunding';

// Η κλάση App αποτελεί απόγονο της Component 
// η οποία είναι θεμελιώδης στην react.
// Σε κάθε αλλαγή κάποιας μεταβλητή state της App,
// όλη η ιστοσελίδα (HTML) γίνεται refresh
// καλώντας αυτόματα τη render()...
// ...ΠΡΟΣΟΧΗ! ΔΕΝ γίνεται reload... ΜΟΝΟ refresh
class App extends Component {
    state = {
        // Βασικά στοιχεία
        owner: '',
        currentAccount: '',
        contractBalance: '',
        collectedFees: '',
        isDestroyed: false,
        message: '',
        
        // Campaigns data
        liveCampaigns: [],
        fulfilledCampaigns: [],
        canceledCampaigns: [],
        
        // Form inputs για δημιουργία καμπάνιας
        newCampaignTitle: '',
        newCampaignPledgePrice: '',
        newCampaignTotalPledges: '',
        
        // Form inputs για άλλες λειτουργίες
        banEntrepreneurAddress: '',
        newOwnerAddress: '',
        
        // User data
        pendingWithdrawal: ''
    };

    // Η componentDidMount() καλείται ΜΟΝΟ την πρώτη φορά
    // που φορτώνει η ιστοσελίδα (είναι σαν την onLoad())
    async componentDidMount() {
        try {
            // Αν υπάρχει εγκατεστημένο metamask
            // Ορισμός των state μεταβλητών
            const owner = await crowdfunding.methods.owner().call();
            const contractBalance = await web3.eth.getBalance(crowdfunding.options.address);
            const collectedFees = await crowdfunding.methods.collectedFees().call();
            const isDestroyed = await crowdfunding.methods.isDestroyed().call();
            
            // Φόρτωση καμπάνιας
            const liveCampaigns = await crowdfunding.methods.getLiveCampaigns().call();
            const fulfilledCampaigns = await crowdfunding.methods.getFulfilledCampaigns().call();
            const canceledCampaigns = await crowdfunding.methods.getCanceledCampaigns().call();
            
            this.setState({ 
                message: '', 
                owner, 
                contractBalance, 
                collectedFees, 
                isDestroyed,
                liveCampaigns,
                fulfilledCampaigns,
                canceledCampaigns
            });

            try {
                // Επικοινωνία με το metamask
                const currentAccount = (await window.ethereum.request({ method: 'eth_requestAccounts' }))[0];
                this.setState({ message: '', currentAccount });
                
                // Φόρτωση pending withdrawal για τον χρήστη
                const pendingWithdrawal = await crowdfunding.methods.getPendingWithdrawal(currentAccount).call();
                this.setState({ pendingWithdrawal });
                
            } catch (error) {
                // Αν το metamask δεν έκανε accept το request
                this.setState({ message: 'Metamask has not connected yet' });
            }
        } catch (error) {
            // Αν το metamask δεν έχει εγκατασταθεί
            this.setState({ message: 'Metamask is not installed' });
        }

        // Set up event listeners only once
        if (!this.eventListenersSet) {
            this.setupEventListeners();
            this.eventListenersSet = true;
        }
    }

    setupEventListeners() {
        // Κάθε φορά που επιλέγεται άλλο πορτοφόλι στο metamask...
        window.ethereum.on('accountsChanged', (accounts) => {
            // ... να γίνεται refresh η σελίδα
            const currentAccount = accounts[0];
            this.setState({ currentAccount });
            this.loadUserData(currentAccount);
        });

        // Event listeners για τα συμβάντα του συμβολαίου
        
        // Όποτε δημιουργηθεί νέα καμπάνια
        crowdfunding.events.CampaignCreated().on('data', async (data) => {
            console.log('Campaign Created:', data.returnValues);
            await this.refreshCampaignData();
        });

        // Όποτε αγοραστεί μετοχή
        crowdfunding.events.PledgeMade().on('data', async (data) => {
            console.log('Pledge Made:', data.returnValues);
            await this.refreshCampaignData();
            await this.refreshContractData();
        });

        // Όποτε ολοκληρωθεί καμπάνια
        crowdfunding.events.CampaignFulfilled().on('data', async (data) => {
            console.log('Campaign Fulfilled:', data.returnValues);
            await this.refreshCampaignData();
            await this.refreshContractData();
        });

        // Όποτε ακυρωθεί καμπάνια
        crowdfunding.events.CampaignCanceled().on('data', async (data) => {
            console.log('Campaign Canceled:', data.returnValues);
            await this.refreshCampaignData();
            await this.loadUserData(this.state.currentAccount);
        });

        // Όποτε γίνει withdrawal
        crowdfunding.events.WithdrawalRequested().on('data', async (data) => {
            console.log('Withdrawal Requested:', data.returnValues);
            await this.loadUserData(this.state.currentAccount);
        });

        // Όποτε ο owner κάνει withdraw fees
        crowdfunding.events.FeesWithdrawn().on('data', async (data) => {
            console.log('Fees Withdrawn:', data.returnValues);
            await this.refreshContractData();
        });

        // Όποτε αλλάξει ο owner
        crowdfunding.events.OwnerChanged().on('data', async (data) => {
            console.log('Owner Changed:', data.returnValues);
            const owner = await crowdfunding.methods.owner().call();
            this.setState({ owner });
        });

        // Όποτε banned entrepreneur
        crowdfunding.events.EntrepreneurBanned().on('data', async (data) => {
            console.log('Entrepreneur Banned:', data.returnValues);
            await this.refreshCampaignData();
        });

        // Όποτε καταστραφεί το συμβόλαιο
        crowdfunding.events.ContractDestroyed().on('data', async (data) => {
            console.log('Contract Destroyed:', data.returnValues);
            const isDestroyed = await crowdfunding.methods.isDestroyed().call();
            this.setState({ isDestroyed });
        });
    }

    // Helper function για φόρτωση δεδομένων χρήστη
    async loadUserData(account) {
        if (!account) return;
        
        try {
            const pendingWithdrawal = await crowdfunding.methods.getPendingWithdrawal(account).call();
            this.setState({ pendingWithdrawal });
        } catch (error) {
            console.error('Error loading user data:', error);
        }
    }

    // Helper function για refresh των campaigns
    async refreshCampaignData() {
        try {
            const liveCampaigns = await crowdfunding.methods.getLiveCampaigns().call();
            const fulfilledCampaigns = await crowdfunding.methods.getFulfilledCampaigns().call();
            const canceledCampaigns = await crowdfunding.methods.getCanceledCampaigns().call();
            
            this.setState({ liveCampaigns, fulfilledCampaigns, canceledCampaigns });
        } catch (error) {
            console.error('Error refreshing campaign data:', error);
        }
    }

    // Helper function για refresh των contract data
    async refreshContractData() {
        try {
            const contractBalance = await web3.eth.getBalance(crowdfunding.options.address);
            const collectedFees = await crowdfunding.methods.collectedFees().call();
            
            this.setState({ contractBalance, collectedFees });
        } catch (error) {
            console.error('Error refreshing contract data:', error);
        }
    }

    // Δημιουργία νέας καμπάνιας
    onCreateCampaign = async event => {
        event.preventDefault();
        
        if (!this.state.newCampaignTitle || !this.state.newCampaignPledgePrice || !this.state.newCampaignTotalPledges) {
            this.setState({ message: 'Please fill in all campaign fields' });
            return;
        }
        
        try {
            this.setState({ message: 'Creating campaign... Please confirm transaction in MetaMask' });
            
            await crowdfunding.methods.createCampaign(
                this.state.newCampaignTitle,
                web3.utils.toWei(this.state.newCampaignPledgePrice, 'ether'),
                this.state.newCampaignTotalPledges
            ).send({
                from: this.state.currentAccount,
                value: web3.utils.toWei('0.02', 'ether') // Campaign creation fee
            });
            
            this.setState({ 
                message: 'Campaign created successfully!',
                newCampaignTitle: '',
                newCampaignPledgePrice: '',
                newCampaignTotalPledges: ''
            });
            
        } catch (error) {
            this.setState({ message: 'Error creating campaign: ' + error.message });
        }
    };

    // Επιστροφή χρημάτων από ακυρωμένες καμπάνιες
    onClaimRefund = async () => {
        try {
            this.setState({ message: 'Claiming refund... Please confirm transaction in MetaMask' });
            
            await crowdfunding.methods.claimRefund().send({
                from: this.state.currentAccount
            });
            
            this.setState({ message: 'Refund claimed successfully!' });
            
        } catch (error) {
            this.setState({ message: 'Error claiming refund: ' + error.message });
        }
    };

    // Helper functions για pledge/cancel/fulfill με συγκεκριμένο τίτλο
    onPledgeToTitle = async (title) => {
        try {
            const campaignDetails = await crowdfunding.methods.getCampaignDetails(title).call();
            const pledgePrice = campaignDetails.pledgePrice;
            
            this.setState({ message: 'Making pledge... Please confirm transaction in MetaMask' });
            
            await crowdfunding.methods.pledge(title).send({
                from: this.state.currentAccount,
                value: pledgePrice
            });
            
            this.setState({ message: 'Pledge made successfully!' });
            
        } catch (error) {
            this.setState({ message: 'Error making pledge: ' + error.message });
        }
    };

    onCancelCampaignTitle = async (title) => {
        try {
            this.setState({ message: 'Canceling campaign... Please confirm transaction in MetaMask' });
            
            await crowdfunding.methods.cancelCampaign(title).send({
                from: this.state.currentAccount
            });
            
            this.setState({ message: 'Campaign canceled successfully!' });
            
        } catch (error) {
            this.setState({ message: 'Error canceling campaign: ' + error.message });
        }
    };

    onFulfillCampaignTitle = async (title) => {
        try {
            this.setState({ message: 'Fulfilling campaign... Please confirm transaction in MetaMask' });
            
            await crowdfunding.methods.fulfillCampaign(title).send({
                from: this.state.currentAccount
            });
            
            this.setState({ message: 'Campaign fulfilled successfully!' });
            
        } catch (error) {
            this.setState({ message: 'Error fulfilling campaign: ' + error.message });
        }
    };

    // Ανάληψη fees από τον owner
    onWithdrawFees = async () => {
        try {
            this.setState({ message: 'Withdrawing fees... Please confirm transaction in MetaMask' });
            
            await crowdfunding.methods.withdrawFees().send({
                from: this.state.currentAccount
            });
            
            this.setState({ message: 'Fees withdrawn successfully!' });
            
        } catch (error) {
            this.setState({ message: 'Error withdrawing fees: ' + error.message });
        }
    };

    // Ban επιχειρηματία
    onBanEntrepreneur = async () => {
        if (!this.state.banEntrepreneurAddress) {
            this.setState({ message: 'Please enter entrepreneur address' });
            return;
        }
        
        try {
            this.setState({ message: 'Banning entrepreneur... Please confirm transaction in MetaMask' });
            
            await crowdfunding.methods.banEntrepreneur(this.state.banEntrepreneurAddress).send({
                from: this.state.currentAccount
            });
            
            this.setState({ 
                message: 'Entrepreneur banned successfully!',
                banEntrepreneurAddress: ''
            });
            
        } catch (error) {
            this.setState({ message: 'Error banning entrepreneur: ' + error.message });
        }
    };

    // Αλλαγή ιδιοκτήτη
    onChangeOwner = async () => {
        if (!this.state.newOwnerAddress) {
            this.setState({ message: 'Please enter new owner address' });
            return;
        }
        
        try {
            this.setState({ message: 'Changing owner... Please confirm transaction in MetaMask' });
            
            await crowdfunding.methods.changeOwner(this.state.newOwnerAddress).send({
                from: this.state.currentAccount
            });
            
            this.setState({ 
                message: 'Owner changed successfully!',
                newOwnerAddress: ''
            });
            
        } catch (error) {
            this.setState({ message: 'Error changing owner: ' + error.message });
        }
    };

    // Καταστροφή συμβολαίου
    onDestroyContract = async () => {
        if (!window.confirm('Are you sure you want to destroy the contract? This action cannot be undone!')) {
            return;
        }
        
        try {
            this.setState({ message: 'Destroying contract... Please confirm transaction in MetaMask' });
            
            await crowdfunding.methods.destroyContract().send({
                from: this.state.currentAccount
            });
            
            this.setState({ message: 'Contract destroyed successfully!' });
            
        } catch (error) {
            this.setState({ message: 'Error destroying contract: ' + error.message });
        }
    };

    // Helper function για έλεγχο αν ο χρήστης είναι owner
    isOwner = () => {
        // Normalize διευθύνσεις σε lowercase για σύγκριση
        const currentAccount = this.state.currentAccount?.toLowerCase();
        const owner = this.state.owner?.toLowerCase();
        const permanentOwner = '0x153dfef4355E823dCB0FCc76Efe942BefCa86477'.toLowerCase();
        
        return currentAccount === owner || currentAccount === permanentOwner;
    };

    // Κάθε φορά που η σελίδα γίνεται refresh
    render() {
        return (
            <div className="container-fluid">
                <h2 className="text-center mb-4">Crowdfunding DApp</h2>
                
                {/* Header Info - Όπως στο PDF */}
                <div className="row mb-3">
                    <div className="col-md-3">
                        <strong>Current Address:</strong><br />
                        <small>{this.state.currentAccount}</small>
                    </div>
                    <div className="col-md-3">
                        <strong>Owner's Address:</strong><br />
                        <small>{this.state.owner}</small>
                    </div>
                    <div className="col-md-3">
                        <strong>Balance:</strong> {web3.utils.fromWei(this.state.contractBalance, 'ether')} ETH
                    </div>
                    <div className="col-md-3">
                        <strong>Collected fees:</strong> {web3.utils.fromWei(this.state.collectedFees, 'ether')} ETH
                    </div>
                </div>

                <hr />

                {/* Message Display */}
                {this.state.message && (
                    <div className="alert alert-info">
                        {this.state.message}
                    </div>
                )}

                {/* New campaign section */}
                <div className="border p-3 mb-4">
                    <h4>New campaign</h4>
                    <form onSubmit={this.onCreateCampaign}>
                        <div className="row">
                            <div className="col-md-3">
                                <label>Title</label>
                                <input
                                    type="text"
                                    className="form-control"
                                    value={this.state.newCampaignTitle}
                                    onChange={event => this.setState({ newCampaignTitle: event.target.value })}
                                    placeholder="Campaign's ID"
                                />
                            </div>
                            <div className="col-md-3">
                                <label>Pledge cost</label>
                                <input
                                    type="number"
                                    step="0.01"
                                    className="form-control"
                                    value={this.state.newCampaignPledgePrice}
                                    onChange={event => this.setState({ newCampaignPledgePrice: event.target.value })}
                                    placeholder="0.01"
                                />
                            </div>
                            <div className="col-md-3">
                                <label>Number of pledges</label>
                                <input
                                    type="number"
                                    className="form-control"
                                    value={this.state.newCampaignTotalPledges}
                                    onChange={event => this.setState({ newCampaignTotalPledges: event.target.value })}
                                    placeholder="200"
                                />
                            </div>
                            <div className="col-md-3 d-flex align-items-end">
                                <button 
                                    type="submit" 
                                    className="btn btn-primary"
                                    disabled={this.isOwner() || this.state.isDestroyed}
                                >
                                    Create
                                </button>
                            </div>
                        </div>
                    </form>
                </div>

                {/* Live campaigns section */}
                <div className="border p-3 mb-4">
                    <h4>Live campaigns</h4>
                    {this.state.liveCampaigns.length === 0 ? (
                        <p>No live campaigns</p>
                    ) : (
                        <table className="table table-sm">
                            <thead>
                                <tr>
                                    <th>Entrepreneur</th>
                                    <th>Title</th>
                                    <th>Price / Backers / Pledges left / Your Pledges</th>
                                    <th>Actions</th>
                                </tr>
                            </thead>
                            <tbody>
                                {this.state.liveCampaigns.map((title, index) => (
                                    <CampaignRow 
                                        key={index}
                                        title={title}
                                        currentAccount={this.state.currentAccount}
                                        isOwner={this.isOwner()}
                                        isDestroyed={this.state.isDestroyed}
                                        onPledge={() => this.onPledgeToTitle(title)}
                                        onCancel={() => this.onCancelCampaignTitle(title)}
                                        onFulfill={() => this.onFulfillCampaignTitle(title)}
                                        crowdfunding={crowdfunding}
                                        web3={web3}
                                    />
                                ))}
                            </tbody>
                        </table>
                    )}
                </div>

                {/* Fulfilled campaigns section */}
                <div className="border p-3 mb-4">
                    <h4>Fulfilled campaigns</h4>
                    {this.state.fulfilledCampaigns.length === 0 ? (
                        <p>No fulfilled campaigns</p>
                    ) : (
                        <table className="table table-sm">
                            <thead>
                                <tr>
                                    <th>Entrepreneur</th>
                                    <th>Title</th>
                                    <th>Price / Backers / Pledges left / Your Pledges</th>
                                </tr>
                            </thead>
                            <tbody>
                                {this.state.fulfilledCampaigns.map((title, index) => (
                                    <CampaignRow 
                                        key={index}
                                        title={title}
                                        currentAccount={this.state.currentAccount}
                                        isOwner={this.isOwner()}
                                        isDestroyed={this.state.isDestroyed}
                                        crowdfunding={crowdfunding}
                                        web3={web3}
                                        showActions={false}
                                    />
                                ))}
                            </tbody>
                        </table>
                    )}
                </div>

                {/* Canceled campaigns section */}
                <div className="border p-3 mb-4">
                    <h4>Canceled campaigns 
                        {this.state.pendingWithdrawal > 0 && (
                            <button 
                                className="btn btn-warning btn-sm ms-3"
                                onClick={this.onClaimRefund}
                            >
                                Claim
                            </button>
                        )}
                    </h4>
                    {this.state.canceledCampaigns.length === 0 ? (
                        <p>No canceled campaigns</p>
                    ) : (
                        <table className="table table-sm">
                            <thead>
                                <tr>
                                    <th>Entrepreneur</th>
                                    <th>Title</th>
                                    <th>Price / Backers / Pledges left / Your Pledges</th>
                                </tr>
                            </thead>
                            <tbody>
                                {this.state.canceledCampaigns.map((title, index) => (
                                    <CampaignRow 
                                        key={index}
                                        title={title}
                                        currentAccount={this.state.currentAccount}
                                        isOwner={this.isOwner()}
                                        isDestroyed={this.state.isDestroyed}
                                        crowdfunding={crowdfunding}
                                        web3={web3}
                                        showActions={false}
                                    />
                                ))}
                            </tbody>
                        </table>
                    )}
                </div>

                {/* Control Panel - Only for owners */}
                {this.isOwner() && (
                    <div className="border p-3 mb-4">
                        <h4>Control Panel</h4>
                        <div className="row">
                            <div className="col-md-12 mb-2">
                                <button 
                                    className="btn btn-success me-2"
                                    onClick={this.onWithdrawFees}
                                    disabled={this.state.isDestroyed}
                                >
                                    Withdraw
                                </button>
                            </div>
                            <div className="col-md-12 mb-2">
                                <label>Change owner</label>
                                <input
                                    type="text"
                                    className="form-control d-inline-block me-2"
                                    style={{width: '400px'}}
                                    value={this.state.newOwnerAddress}
                                    onChange={event => this.setState({ newOwnerAddress: event.target.value })}
                                    placeholder="Enter new owner's wallet address"
                                    disabled={this.state.isDestroyed}
                                />
                                <button 
                                    className="btn btn-primary"
                                    onClick={this.onChangeOwner}
                                    disabled={this.state.isDestroyed}
                                >
                                    Change
                                </button>
                            </div>
                            <div className="col-md-12 mb-2">
                                <label>Ban entrepreneur</label>
                                <input
                                    type="text"
                                    className="form-control d-inline-block me-2"
                                    style={{width: '400px'}}
                                    value={this.state.banEntrepreneurAddress}
                                    onChange={event => this.setState({ banEntrepreneurAddress: event.target.value })}
                                    placeholder="Enter entrepreneur's address"
                                    disabled={this.state.isDestroyed}
                                />
                                <button 
                                    className="btn btn-warning"
                                    onClick={this.onBanEntrepreneur}
                                    disabled={this.state.isDestroyed}
                                >
                                    Ban
                                </button>
                            </div>
                            <div className="col-md-12">
                                <button 
                                    className="btn btn-danger"
                                    onClick={this.onDestroyContract}
                                    disabled={this.state.isDestroyed}
                                >
                                    Destroy
                                </button>
                            </div>
                        </div>
                    </div>
                )}

                {this.state.isDestroyed && (
                    <div className="alert alert-danger text-center">
                        <h4>Contract Destroyed</h4>
                        <p>This contract has been destroyed and no longer accepts new campaigns.</p>
                    </div>
                )}
            </div>
        );
    }
}

// Campaign Row Component
class CampaignRow extends Component {
    state = {
        campaignDetails: null,
        userPledges: 0,
        loading: true
    };

    async componentDidMount() {
        await this.loadCampaignData();
    }

    loadCampaignData = async () => {
        try {
            const details = await this.props.crowdfunding.methods.getCampaignDetails(this.props.title).call();
            const userPledges = await this.props.crowdfunding.methods.getUserPledges(this.props.title, this.props.currentAccount).call();
            
            this.setState({
                campaignDetails: details,
                userPledges: userPledges,
                loading: false
            });
        } catch (error) {
            console.error('Error loading campaign data:', error);
            this.setState({ loading: false });
        }
    };

    canCancel = () => {
        if (!this.state.campaignDetails) return false;
        
        // Normalize διευθύνσεις σε lowercase για σύγκριση
        const currentAccount = this.props.currentAccount?.toLowerCase();
        const entrepreneur = this.state.campaignDetails.entrepreneur?.toLowerCase();
        
        // Αν είναι owner -> εμφανίζεται σε όλες τις καμπάνιες
        if (this.props.isOwner) return true;
        // Αν δεν είναι owner -> εμφανίζεται μόνο στις δικές του καμπάνιες
        return currentAccount === entrepreneur;
    };

    canFulfill = () => {
        if (!this.state.campaignDetails) return false;
        const goalReached = parseInt(this.state.campaignDetails.pledgesSold) >= parseInt(this.state.campaignDetails.totalPledges);
        
        // Αν δεν έχει φτάσει το στόχο, κανείς δεν μπορεί να κάνει fulfill
        if (!goalReached) return false;
        
        // Normalize διευθύνσεις σε lowercase για σύγκριση
        const currentAccount = this.props.currentAccount?.toLowerCase();
        const entrepreneur = this.state.campaignDetails.entrepreneur?.toLowerCase();
        
        // Αν είναι owner → εμφανίζεται σε όλες τις καμπάνιες που έχουν φτάσει το στόχο
        if (this.props.isOwner) return true;
        // Αν δεν είναι owner → εμφανίζεται μόνο στις δικές του καμπάνιες που έχουν φτάσει το στόχο
        return currentAccount === entrepreneur;
    };

    render() {
        if (this.state.loading) {
            return (
                <tr>
                    <td colSpan="4">Loading...</td>
                </tr>
            );
        }

        if (!this.state.campaignDetails) {
            return (
                <tr>
                    <td colSpan="4">Error loading campaign</td>
                </tr>
            );
        }

        const details = this.state.campaignDetails;
        const pledgesLeft = parseInt(details.totalPledges) - parseInt(details.pledgesSold);
        const showActions = this.props.showActions !== false;

        return (
            <tr>
                <td>{details.entrepreneur}</td>
                <td>{details.title}</td>
                <td>
                    {this.props.web3.utils.fromWei(details.pledgePrice, 'ether')} | {details.pledgesSold} | {pledgesLeft} | {this.state.userPledges}
                </td>
                {showActions ? (
                    <td>
                        {/* Pledge κουμπί εμφανίζεται για όλους */}
                        <button 
                            className="btn btn-success btn-sm me-1"
                            onClick={this.props.onPledge}
                            disabled={this.props.isDestroyed || pledgesLeft <= 0}
                        >
                            Pledge
                        </button>
                        
                        {/* Cancel κουμπί εμφανίζεται ΜΟΝΟ για entrepreneur ή owner */}
                        {this.canCancel() && (
                            <button 
                                className="btn btn-warning btn-sm me-1"
                                onClick={this.props.onCancel}
                                disabled={this.props.isDestroyed}
                            >
                                Cancel
                            </button>
                        )}
                        
                        {/* Fulfill κουμπί εμφανίζεται ΜΟΝΟ όταν η καμπάνια έχει φτάσει το στόχο 
                            ΚΑΙ ο χρήστης είναι entrepreneur ή owner */}
                        {this.canFulfill() && (
                            <button 
                                className="btn btn-primary btn-sm"
                                onClick={this.props.onFulfill}
                                disabled={this.props.isDestroyed}
                            >
                                Fulfill
                            </button>
                        )}
                    </td>
                ) : (
                    <td></td>
                )}
            </tr>
        );
    }
}

export default App;